.switch-button {
    background: rgba(100, 182, 172, 0.56);
    overflow: hidden;
    width: 240px;
    text-align: center;
    font-size: 0.80rem;
    letter-spacing: 1px;
    color: #000000;
    position: relative;
    padding-right: 120px;
    position: relative;
}
.switch-button:before {
    content: "Fahrenheit";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 120px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;
    pointer-events: none;
}
  
.switch-button-checkbox {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 2;
}

.switch-button-checkbox:checked + .switch-button-label:before {
    transform: translateX(120px);
    transition: transform 300ms linear;
}
  
.switch-button-checkbox + .switch-button-label {
    position: relative;
    padding: 5px 2px;
    display: block;
    user-select: none;
    pointer-events: none;
}

.switch-button-checkbox:before, .switch-button-label:before {
    content: "";
    background: #64B6AC;
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    transform: translateX(0);
    transition: transform 300ms;
}
  
.switch-button-label-span {
    position: relative;
}